<script>
import MoneyIcon from "@/@core/components/svg-icons/MoneyIcon.vue";
import {
    BButton,
    BCard,
    BFormGroup,
    BForm,
    BFormInput,
    BAvatar,
    BAlert,
    BCol,
    BFormRow,
    BFormValidFeedback,
    BFormCheckbox,
    BTable,
    BCollapse,
} from "bootstrap-vue";
import { v4 as uuidv4 } from "uuid";
import CustomVueSelect from "@core/components/custom-vue-select/CustomVueSelect.vue";
import SelectableSearch from "@/@core/components/selectable-search/SelectableSearch.vue";
import SearchInput from "@/@core/components/search-input/SearchInput.vue";
import vSelect from "vue-select";

import ListIcon from "@/@core/components/svg-icons/ListIcon.vue";
import _ from "lodash";
import ReceiveDebtSuccess from "./ReceiveDebtSuccess.vue";
import VuePersianDatetimePicker from "vue-persian-datetime-picker";
import { separator } from "@core/helper";

export default {
    components: {
        datePicker: VuePersianDatetimePicker,
        MoneyIcon,
        BButton,
        BCard,
        BFormGroup,
        BForm,
        BFormInput,
        BAvatar,
        BAlert,
        CustomVueSelect,
        BCol,
        BFormRow,
        BFormValidFeedback,
        SelectableSearch,
        ReceiveDebtSuccess,
        BFormCheckbox,
        BTable,
        ListIcon,
        BCollapse,
        SearchInput,
        vSelect,
    },
    data() {
        return {
            incomeDate: '',
            controller: null,
            debtorsList: [],
            debtorListFields: ["name", "fee"],
            paymentMethod: [""],
            newReceiveDebt: {
                user_id: "",
                fee: "",
                fee_date: "",
                receipt_type_id: "",
                receipt: "",
                user_unit_debt_id: [],
            },
            reasonsField: ["checkbox", "icon", "title", "fee"],
            successInfo: {
                msg: "",
                personName: "",
                receiveFee: "",
                reason: "",
                link: "",
                receiptType: "",
            },
            error: false,
            aPersonSelected: false,
            successfulSubmit: false,
            searchInput: "",
            loading: false,
            isUserSelected: false,
            selectedUser: "",
            userUnitDebtDetails: [],
            sendFormLoading: false,
            receiptTypeData: [],
            user_credit: "",
            locale_user_credit: "",
            searchResults: [],
            token: '',
        };
    },
    watch: {
        searchInput(val) {
            if (val !== "") {
                this.callDebounceSearch(val);
            }
        },
    },
    async created() {
        this.token = localStorage.getItem('token')
        try {
            if (this.$route.query.user_id && this.$route.query.name) {
                const user = {
                    user_id: this.$route.query.user_id,
                    name: this.$route.query.name
                }
                this.setActiveUser(user)
            }
            const currentMonthId = new Date()
                .toLocaleString("fa-IR-u-nu-latn")
                .replace(',', '')
                .split("")[0]
                .split("/")[1];
            this.loading = true;
            // const { data: userUnitDebt } = await this.$http.get(
            //   "manager/user-debt/user-unit-debt"
            // );
            const { data: receiptType } = await this.$http.get(
                "manager/common/receipt-type", {
                    headers: {
                        'Authorization': 'Bearer ' + this.token
                    }
                }
            );
            this.receiptTypeData = Object.entries(receiptType.list)
                .map((item) => ({
                    key: item[0],
                    value: item[1],
                }));

            // userUnitDebt.list.forEach((item) => {
            //   // eslint-disable-next-line no-param-reassign
            //   item.isSelected = false;
            // });

            // this.userUnitDebtDetails = userUnitDebt.list.map(item => ({
            //     ...item,
            //     isSelected: false
            //   }));

            const { data: UserDebt } = await this.$http.get("manager/user-debt", {
                params: {
                    all_users: 0,
                },
                headers: {
                    'Authorization': 'Bearer ' + this.token
                }
            });
            this.debtorsList = UserDebt.debtor_list.list;

            this.loading = false;
        } catch (err) {
            console.log(err);
        }
    },

    mounted() {
    },
    methods: {
        separate() {
            this.newReceiveDebt.fee = separator(this.newReceiveDebt.fee)
        },
        getUniqueString() {
            return uuidv4();
        },
        async submitForm() {
            // console.log()

            try {
                this.sendFormLoading = true;
                const selectedUserUnit = this.userUnitDebtDetails.filter(
                    (item) => item.isSelected
                );
                const listOfUserUnitID = selectedUserUnit.map(
                    (item) => item.user_unit_debt_id
                );
                this.newReceiveDebt.user_unit_debt_id = listOfUserUnitID;

                if (this.newReceiveDebt?.receipt_type_id?.targetValue) {
                    this.newReceiveDebt.receipt_type_id = this.newReceiveDebt.receipt_type_id.targetValue;
                }
                this.newReceiveDebt.fee_date = this.incomeDate

                this.newReceiveDebt.user_id = this.selectedUser.user_id;
                const { data } = await this.$http.post(
                    "/manager/income/store",
                    this.newReceiveDebt,
                    {
                        headers: {
                            'Authorization': 'Bearer ' + this.token
                        }
                    }
                );
                if (data.status === "success") {
                    this.successfulSubmit = true;
                    this.successInfo.personName = data.name;
                    this.successInfo.msg = data.message;
                    this.successInfo.reason = data.comment;
                    this.successInfo.link = data.download_link;
                    this.successInfo.receiveFee = data.creditor;
                    this.successInfo.usedCredit = data.used_credit;
                    this.successInfo.receiptType = data.receipt_type;
                    this.successInfo.receipt = data.receipt;
                }
                this.sendFormLoading = false;

                // console.log(this.newReceiveDebt);
            } catch (err) {
                this.sendFormLoading = false;
                console.log(err);
            }

            // this.successfulSubmit = true;
        },
        onDone() {
            this.$router.push({ name: "receiveDebt" });
        },
        toDebtorPage() {
            this.$router.push({ name: "debtor" });
        },
        async setActiveUser(user) {
            // console.log(user);
            this.selectedUser = user;
            try {
                const { data: userUnitDebt } = await this.$http.get(
                    "/manager/user-debt/user-unit-debt",
                    {
                        params: {
                            user_id: user.user_id,
                        },
                        headers: {
                            'Authorization': 'Bearer ' + this.token
                        }
                    }
                );
                userUnitDebt.list.forEach((item) => {
                    // eslint-disable-next-line no-param-reassign
                    item.isSelected = false;
                });

                this.userUnitDebtDetails = userUnitDebt.list.map((item) => ({
                    ...item,
                    isSelected: false,
                }));
                this.user_credit = userUnitDebt.user_credit;
                this.locale_user_credit = userUnitDebt.locale_user_credit;
            } catch (err) {
                console.log(err);
            }
        },
        goToDetailPage(id) {
            this.$router.push({
                name: "debtor",
                query: { id }
            });
        },
        async searchUsersApi(search) {
            try {
                if (this.controller) {
                    this.controller.abort();
                }
                this.controller = new AbortController();

                fetch(
                    `https://sm.oop.ir/api/v1/manager/user-debt?all_users=1&search=${search}`,
                    {
                        headers: new Headers({
                            Authorization: `Bearer ${localStorage.getItem("token")}`,
                        }),
                        signal: this.controller.signal,
                    }
                )
                    .then((response) => response.json())
                    .then((data) => {
                        // console.log(`Results for ${search}`, data);
                        this.searchResults = data.debtor_list.list;
                    })
                    .catch((err) => {
                        console.log(err);
                    });
                // const result = await this.$http.get("manager/user-debt", {
                //   signal: this.controller.signal,
                //   params: {
                //     search,
                //     all_users: 1,
                //   },
                // });
            } catch (err) {
                console.log(err);
            }
        },
        callDebounceSearch: _.debounce(function (search) {
            this.searchUsersApi(search);
        }, 500),
    },
};
</script>
