<!-- eslint-disable vue/html-indent -->

<template>
	<div class="selectable-search" v-click-outside="closeDropdown">
		<b-form-input
			ref="ttt"
			v-model="edit"
			class="search-input"
			type="search"
			:placeholder="placeholder"
			:required="required"
			:state="state"
			@input="onInput"
		/>
		<div v-show="dropdownIsOpen" class="dropdown">
			<ul>
				<li v-for="(item, index) in filteredOptions" :key="index" @click="onChange(item)">
					<slot
						name="option"
						:item="item"
						:value="item"
						:click="onChange.bind(this, item)"
					>
						{{ item }}
					</slot>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import { BFormInput } from "bootstrap-vue";

export default {
	components: {
		BFormInput,
	},
	props: {
		value: {
			type: String,
		},
		placeholder: {
			type: String,
			default: "",
		},
		options: {
			type: Array,
			default: [],
		},
		required: {
			type: Boolean,
			default: false,
		},
		state: {
			type: Boolean,
			default: null,
		},
	},
	data() {
		return {
			dropdownIsOpen: false,
			searchInputValue: this.value,
			filteredOptions: this.options,
			aValueChoosed: false,
		};
	},
	computed: {
		edit: {
			get() {
				return this.searchInputValue;
			},
			set(e) {
				this.searchInputValue = e;
				this.$emit("input", this.searchInputValue);
			},
		},
	},
	watch: {
		aValueChoosed() {
			this.$emit("selected", this.aValueChoosed);
		},
	},
	methods: {
		onChange(item) {
			this.searchInputValue = `${item}`;
			this.edit = `${item}`;
			this.dropdownIsOpen = false;
			this.aValueChoosed = true;
		},
		closeDropdown() {
			this.dropdownIsOpen = false;
		},
		onInput() {
			this.aValueChoosed = false;

			const searchResult = this.options.filter((item) =>
				item.includes(this.searchInputValue)
			);

			if (searchResult.length > 0 && this.searchInputValue.length > 0)
				this.dropdownIsOpen = true;
			else this.dropdownIsOpen = false;

			this.filteredOptions = searchResult;
		},
	},
};
</script>

